.presentacion-container{
    position: relative;
    display: flex;
    align-items: flex-start;

    width: 100%;
    height: calc(100vh + 40px);
    
    background-image: url("../../public/img/Fondo-primeraVista.jpg");
    background-size: cover;
    background-position: left;
    background-repeat: no-repeat;
    /*background-attachment: fixed;*/
}

.presentacion-view-container{
    display: flex;
    justify-content: center;
    align-items: center;

    width: 100%;
    height: 100vh;
}

@media (min-width: 650px){
    .presentacion-view1000{
        display: flex;
        align-items: center;
        justify-content: center;
        min-width: 630px;
        width: 80%;
        height: 650px;
    }

    .presentacion-view1000_img{
        width: 630px;
        height: 100%;

        background-image: url("../../public/img/media1000/presentacion.png");
        background-size: contain;
        background-position: center;
        background-repeat: no-repeat;

    }

    
    .presentacion-foto_polygon-background{
        display: none;
    }

    .presentacion-foto_fotografia{
        display: none;
    }

    .presentacion-foto_kanji{
        display: none;
    }

    .presentacion-foto_err-shadow{
        display: none;
    }

    .presentacion-foto_err-cuadro{
        display: none;
    }

    .presentacion-foto_aceptar-button{
        display: none;
    }

    .presentacion-nombre_introduccion-shadow{
        display: none;
    }

    .presentacion-nombre_introduccion{
        display: none;
    }

    .presentacion-nombre_nombre{
        display: none;
    }

    .presentacion-nombre_apellido{
        display: none;
    }

    .presentacion-nombre_cargo-shadow{
        display: none;
    }

    .presentacion-nombre_cargo{
        display: none;
    }
}

@media (min-width: 1250px) {
    .presentacion-view1000{
        display: none;
    }
    
    .presentacion_content{
        position: relative;
        display: flex;
    
        max-width: 1200px;
        width: 90%;
        height: 660px;
    }
    
    .presentacion-foto_polygon-background{
        position: absolute;
        display: block;
        top: -50px;
        left: 170px;
    
        width: 450px;
        height: 630px;
        transform: rotate(60deg);
        clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
    
        background-color: #FF00B8;
    }
    
    .presentacion-foto_fotografia{
        position: absolute;
        display: block;
        top: 140px;
        left: 115px;
    
        width: 390px;
        height: 390px;
        clip-path: circle(50% at 50% 50%);
    
        background-image: url("../../public/img/myphoto.jpg");
        background-position: left;
        background-repeat: no-repeat;
        background-size: 100%;
    }
    
    .presentacion-foto_kanji{
        position: absolute;
        display: block;
        top: 80px;
        left: 30px;
    
        width: 380px;
        height: 130px;
        background-image: url("../../public/img/mensaje-kanji.png");
        background-size: 380px 130px;
        background-repeat: no-repeat;
    }
    
    .presentacion-foto_err-shadow{
        position: absolute;
        display: block;
        top: 420px;
        left: 340px;
    
        width: 165px;
        height: 108px;
        border-radius: 8px;
    
        background-color: #0C0025;
    }
    
    .presentacion-foto_err-cuadro{
        position: absolute;
        display: flex;
        top: 440px;
        left: 320px;
        justify-content: center;
        align-items: center;
    
        width: 165px;
        height: 108px;
        border-radius: 8px;
    
        font-family: 'Bakbak One', cursive;
        font-size: 5rem;
        font-weight: 400;
        
        box-sizing: border-box;
        background-color: #39005B;
        border: solid 15px #9400DA;
        color: white;
    }
    
    .err-cuadro_red-button{
        position: inherit;
        top: -13px;
        right: -5px;
    
        width: 10px;
        height: 10px;
        border-radius: 50%;
    
        background-color: rgb(255, 0, 98);
    }
    
    .err-cuadro_green-button{
        position: inherit;
        top: -13px;
        right: 10px;
    
        width: 10px;
        height: 10px;
        border-radius: 50%;
    
        background-color: rgb(119, 241, 63);
    }
    
    .err-cuadro_grey-button{
        position: inherit;
        top: -13px;
        right: 25px;
    
        width: 10px;
        height: 10px;
        border-radius: 50%;
    
        background-color: rgb(185, 185, 185);
    }
    
    .presentacion-foto_aceptar-button{
        position: absolute;
        top: 540px;
        left: 360px;
        display: flex;
        justify-content: center;
        align-items: center;
    
        width: 80px;
        height: 30px;
        border-radius: 5px;
    
        font-size: 1.3rem;
        font-family: 'Bakbak One', cursive;
    
        background-color: #FF00B8;
        color: white;
    }
    
    .presentacion-nombre_introduccion-shadow{
        position: absolute;
        display: block;
        top: 180px;
        right: 280px;
    
        width: 250px;
        height: 60px;
        clip-path: polygon(12% 20%, 100% 0, 100% 87%, 0 80%);
    
        background-color: #860388e1;
    }
    
    .presentacion-nombre_introduccion{
        position: absolute;
        display: block;
        top: 188px;
        right: 295px;
    
        width: 270px;
        height: 60px;
        clip-path: polygon(12% 20%, 100% 0, 100% 87%, 0 80%);
    
        background-color: #ff00b7bb;
    }
    
    .presentacion-nombre_introduccion p{
        position: absolute;
        top: 10px;
        right: 10px;
    
        font-family: 'Montserrat', sans-serif;
        font-size: 3.2rem;
    
        color: white;
    }
    
    .presentacion-nombre_nombre{
        position: absolute;
        display: block;
        top: 140px;
        right: 10px;
    
        font-family: 'Bakbak One', cursive;
        font-size: 9.6rem;
    
        color: white;
    }
    
    .presentacion-nombre_apellido{
        position: absolute;
        display: block;
        top: 220px;
        right: 10px;
    
        font-family: 'Bakbak One', cursive;
        font-size: 9.6rem;
    
        color: white;
    }
    
    .presentacion-nombre_cargo-shadow{
        position: absolute;
        display: block;
        top: 360px;
        right: 15px;
    
        width: 450px;
        height: 170px;
        clip-path: polygon(0 0, 100% 0, 100% 87%, 18% 78%);
    
        background-color: #860388e1;
    }
    
    .presentacion-nombre_cargo{
        position: absolute;
        top: 340px;
        right: 10px;
        display: flex;
        justify-content: right;
    
        width: 450px;
        height: 170px;
        clip-path: polygon(0 0, 100% 0, 100% 87%, 18% 78%);
    
        background-color: #ff00b7bb;
    }
    
    .presentacion-nombre_cargo p{    
        margin-top: 20px;
    
        font-family: 'Montserrat', sans-serif;
        font-size: 3rem;
        text-align: right;
    
        color: white;
    }
    
    .presentacion-nombre_cargo h2{
        margin-top: 20px;
        margin-right: 15px;

        font-family: 'Bakbak One', cursive;
        font-size: 5.2rem;
        text-align: right;
        line-height: 45px;
        letter-spacing: 3px;

        color: white;
    }

}

.presentacion-footer{
    position: absolute;
    bottom: 0;
    display: flex;
    flex-direction: column;

    width: 100%;
    height: 40px;
}

.presentacion-footer_div1{
    flex-grow: 1;
    background-color: #F806CC;
}

.presentacion-footer_div2{
    flex-grow: 1;
    background-color: #A91079;
}